@font-face {
  font-family: "Lato";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/lato/lato-regular.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/oswald/oswaldregular.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/oswald/oswaldmedium.woff2") format("woff2");
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.logo {
  text-decoration: none;
  display: grid;
  grid-template-columns: 34px 1fr;
  justify-items: center;
  align-items: center;
}
.logo:hover {
  opacity: 0.8;
}
.logo:active {
  opacity: 0.6;
}
@media (min-width: 758px) {
  .logo {
    grid-template-columns: 70px 1fr;
  }
}

.logo__icon {
  grid-column: 1/2;
}
@media (min-width: 758px) {
  .logo__icon {
    width: 60px;
    height: 50px;
  }
}
@media (min-width: 1270px) {
  .logo__icon {
    width: 70px;
    height: 60px;
  }
}

.logo__text--header {
  grid-column: 2/3;
  margin-left: -12px;
}
@media (min-width: 758px) {
  .logo__text--header {
    margin-left: 0;
  }
  .logo__text--footer {
    width: 142px;
    height: 26px;
  }
}
@media (min-width: 1270px) {
  .logo__text--header {
    margin-left: 14px;
    width: 117px;
    height: 21px;
  }
}

.main-title {
  font-family: "Oswald", sans-serif;
  font-weight: 400;
  font-size: 36px;
  line-height: 36px;
  margin: 0;
  max-width: 500px;
}
@media (min-width: 758px) {
  .main-title {
    font-size: 60px;
    line-height: 60px;
  }
}

.secondary-title {
  font-family: "Oswald", sans-serif;
  font-weight: 400;
  font-size: 36px;
  line-height: 40px;
  color: #000000;
  margin: 0;
}
@media (min-width: 758px) {
  .secondary-title {
    font-size: 60px;
    line-height: 60px;
    max-width: 700px;
  }
}

.button {
  background-color: #68b738;
  font-family: "Oswald", sans-serif;
  text-transform: uppercase;
  color: #ffffff;
  text-decoration: none;
  display: block;
  text-align: center;
  cursor: pointer;
  padding: 10px 0;
}
@media (min-width: 758px) {
  .button {
    padding: 17px 0;
  }
}
.button:hover {
  background-color: #5eaa2f;
}
.button:active {
  color: rgba(255, 255, 255, 0.3);
}
.button--catalog {
  padding: 10px 0;
}
.button--show-more:hover {
  background-color: #ebebeb;
}
.button--show-more:active {
  color: rgba(68, 68, 68, 0.3);
}

.page-header {
  position: relative;
}

.page-header__wrapper {
  padding-top: 10px;
}
@media (min-width: 758px) {
  .page-header__wrapper {
    width: 708px;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top: 0;
    padding-bottom: 0;
    gap: 12px;
  }
}
@media (min-width: 1270px) {
  .page-header__wrapper {
    padding-top: 52px;
    width: 1220px;
  }
}

.page-header__wrapper--close-menu .page-header__main-nav {
  display: none;
}
.page-header__wrapper--close-menu .page-header__toggle {
  display: block;
  background-image: url("../img/sprite.svg#hamburger-menu");
}
@media (min-width: 758px) {
  .page-header__wrapper--close-menu .page-header__main-nav {
    display: block;
  }
  .page-header__wrapper--close-menu .page-header__toggle {
    display: none;
  }
}

.page-header__wrapper--open-menu .page-header__main-nav {
  display: block;
  position: absolute;
  top: 100%;
  left: 0;
  width: 100%;
  z-index: 1;
  background-color: #ffffff;
}
.page-header__wrapper--open-menu .page-header__toggle {
  display: block;
  background-image: url("../img/sprite.svg#close");
}
@media (min-width: 758px) {
  .page-header__wrapper--open-menu .page-header__main-nav {
    position: relative;
    background-color: transparent;
  }
  .page-header__wrapper--open-menu .page-header__toggle {
    display: none;
  }
}

.page-header__logo-btn {
  display: grid;
  grid-template-columns: 34px 1fr 24px;
  align-items: center;
  width: 280px;
  margin: 0 auto;
  padding-bottom: 12px;
  flex-shrink: 0;
  position: relative;
}
.page-header__logo-btn::after {
  content: "";
  position: absolute;
  bottom: 0;
  height: 1px;
  width: 100vw;
  background-color: #e6e6e6;
  left: calc(50% - 50vw);
}
@media (min-width: 758px) {
  .page-header__logo-btn {
    grid-template-columns: 34px 1fr;
    padding-bottom: 0;
    width: auto;
    margin: 0;
  }
  .page-header__logo-btn::after {
    display: none;
  }
}

.page-header__logo {
  grid-column: 1/3;
  grid-row: 1;
}

.page-header__toggle {
  background-repeat: no-repeat;
  background-position: center;
  background-color: transparent;
  width: 24px;
  height: 18px;
  border: none;
  padding: 0;
  cursor: pointer;
  grid-column: 3/4;
  grid-row: 1;
  display: none;
}

.main-nav__list {
  list-style: none;
  padding: 0;
  margin: 0;
}
@media (min-width: 758px) {
  .main-nav__list {
    display: flex;
    flex-wrap: wrap;
    padding-top: 10px;
    justify-content: flex-end;
  }
}

.main-nav__item {
  border-bottom: 1px solid #e6e6e6;
  display: flex;
  justify-content: center;
}
@media (min-width: 758px) {
  .main-nav__item {
    display: block;
    margin-left: 37px;
    border-bottom: none;
    width: auto;
  }
  .main-nav__item:first-child {
    margin-left: 0;
  }
}

@media (min-width: 758px) {
  .main-nav__item--current {
    padding-bottom: 8px;
    border-bottom: 2px solid #68b738;
  }
}
@media (min-width: 1270px) {
  .main-nav__item--current {
    padding-bottom: 5px;
  }
}

@media (min-width: 1270px) {
  .main-nav__item--current-index {
    border-bottom: 2px solid #ffffff;
  }
}

.main-nav__link {
  font-family: "Oswald", sans-serif;
  font-weight: 500;
  font-size: 20px;
  line-height: 20px;
  text-transform: uppercase;
  color: #000000;
  text-decoration: none;
  display: block;
  padding-top: 22px;
  padding-bottom: 24px;
  text-align: center;
}
.main-nav__link:hover {
  opacity: 0.6;
}
.main-nav__link:active {
  opacity: 0.3;
}
@media (min-width: 758px) {
  .main-nav__link {
    font-weight: 400;
    font-size: 18px;
    line-height: 24px;
    padding: 0;
    border-top: none;
    text-align: start;
  }
}
@media (min-width: 1270px) {
  .main-nav__link {
    font-size: 20px;
    line-height: 30px;
  }
  .main-nav__link--light {
    color: #ffffff;
  }
}

.page-footer {
  background-color: #f2f2f2;
}

.page-footer__wrapper {
  padding: 20px 0;
}
@media (min-width: 758px) {
  .page-footer__wrapper {
    display: flex;
    justify-content: space-between;
    padding: 37px 0;
  }
}
@media (min-width: 1270px) {
  .page-footer__wrapper {
    padding: 55px 0;
  }
}

.page-footer__logo {
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 18px 0;
  border-bottom: 1px solid #d9d9d9;
}
@media (min-width: 758px) {
  .page-footer__logo {
    padding: 0;
    border-bottom: none;
  }
}

.page-footer__social-list {
  margin: 0;
  padding: 0;
  list-style: none;
  border-bottom: 1px solid #d9d9d9;
  padding: 20px 0 0;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}
@media (min-width: 758px) {
  .page-footer__social-list {
    padding: 0;
    border-bottom: none;
    margin-left: 27px;
    max-width: 160px;
  }
}
@media (min-width: 1270px) {
  .page-footer__social-list {
    max-width: 500px;
  }
}

.page-footer__copyright-link {
  font-family: "Lato", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 18px;
  text-decoration: none;
  color: #444444;
  width: 100%;
  padding: 28px 0;
  display: block;
  position: relative;
}
.page-footer__copyright-link::after {
  content: "";
  position: absolute;
  mask: url("../img/sprite.svg#academy-logo");
  background-color: #666666;
  background-repeat: no-repeat;
  width: 27px;
  height: 34px;
  right: 0;
  top: 20px;
}
.page-footer__copyright-link:hover::after, .page-footer__copyright-link:focus::after {
  background-color: #68b738;
}
.page-footer__copyright-link:active {
  opacity: 0.3;
}
@media (min-width: 758px) {
  .page-footer__copyright-link {
    width: auto;
    padding-right: 54px;
    display: flex;
    align-items: center;
  }
  .page-footer__copyright-link::after {
    top: 20px;
  }
}

@media (min-width: 1270px) {
  .address {
    padding-top: 95px;
    padding-bottom: 107px;
  }
}

.address__wrapper {
  display: flex;
