}
@media (min-width: 758px) {
  .address__dealers {
    font-size: 20px;
    line-height: 26px;
    margin-right: 130px;
  }
}
@media (min-width: 1270px) {
  .address__dealers {
    margin-right: 0;
  }
}

.address__location {
  font-family: "Lato", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  color: #444444;
  font-style: normal;
  margin-left: 17px;
}
@media (min-width: 758px) {
  .address__location {
    font-size: 16px;
    line-height: 20px;
    margin-left: 125px;
    text-align: right;
  }
}
@media (min-width: 1270px) {
  .address__location {
