  }
}

.social-list__item {
  margin-right: 25px;
  margin-bottom: 20px;
}
.social-list__item:last-child {
  margin-right: 0;
}
@media (min-width: 758px) {
  .social-list__item {
    margin-top: 20px;
  }
}

.social-list__link {
  width: 28px;
  height: 22px;
  padding: 4px;
  display: flex;
  background-repeat: no-repeat;
  background-position: center;
  background-color: #666666;
}
.social-list__link:hover, .social-list__link:focus {
  background-color: #68b738;
}
.social-list__link:active {
  opacity: 0.3;
}
.social-list__link--vkontakte {
  mask: url("../img/sprite.svg#vkontakte");
}
.social-list__link--youtube {
  mask: url("../img/sprite.svg#youtube");
}
.social-list__link--telegram {
  mask: url("../img/sprite.svg#telegram");
}

.address-map {
  position: relative;
  z-index: 0;
  background-color: #f2f2f2;
}
.address-map--index {
  background-color: #ffffff;
}

.address-map__image {
  width: 100%;
  height: auto;
  display: block;
}

.address-map__map {
  border: none;
  height: 362px;
  min-width: 100%;
  min-height: 100%;
  overflow: hidden;
  margin-bottom: -6px;
}
@media (min-width: 758px) {
  .address-map__map {
    height: 400px;
  }
}
@media (min-width: 1270px) {
  .address-map__map {
    position: absolute;
    top: 0;
    bottom: 0;
    z-index: 1;
  }
}

.address-map__map-noscript {
  width: 100%;
  height: 362px;
  position: absolute;
  bottom: 0;
  left: 0;
  z-index: 1;
}
@media (min-width: 758px) {
  .address-map__map-noscript {
    height: 400px;
  }
}

.address-map__img-noscript {
  width: 100%;
  height: 100%;
  z-index: 2;
  object-fit: cover;
}
@media (min-width: 1270px) {
  .address-map__img-noscript {
    z-index: 1;
  }
}

.page-main {
  overflow: hidden;
}

@media (min-width: 758px) {
  .page-main__wrapper--form {
    padding: 0 40px;
  }
}
@media (min-width: 1270px) {
  .page-main__wrapper--form {
    padding: 0;
  }
}

.page-main__title {
  margin-top: 27px;
}
.page-main__title--form {
  margin-bottom: 37px;
}
.page-main__title--catalog {
  margin-bottom: 41px;
}
@media (min-width: 758px) {
  .page-main__title {
    margin-top: 62px;
  }
  .page-main__title--form {
    margin-bottom: 54px;
  }
  .page-main__title--catalog {
    margin-bottom: 82px;
  }
}
@media (min-width: 1270px) {
  .page-main__title {
    margin-top: 70px;
  }
  .page-main__title--form {
    margin-bottom: 56px;
  }
  .page-main__title--catalog {
    margin-bottom: 84px;
  }
}

.page-main__subheader {
  font-family: "Oswald", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  text-transform: uppercase;
  margin: 0;
  margin-bottom: 39px;
  margin-right: 50px;
}
@media (min-width: 758px) {
  .page-main__subheader {
    font-size: 20px;
    line-height: 30px;
    margin-bottom: 60px;
    margin-right: 300px;
  }
}
@media (min-width: 1270px) {
  .page-main__subheader {
    margin-right: 900px;
    margin-bottom: 62px;
  }
}

.about__wrapper {
  padding-bottom: 49px;
}
@media (min-width: 758px) {
  .about__wrapper {
    padding-bottom: 47px;
  }
}

.about__title {
  margin-bottom: 40px;
}
@media (min-width: 758px) {
  .about__title {
    margin-bottom: 72px;
  }
}

.about__list {
  margin: 0;
  padding: 0;
  list-style: none;
}
@media (min-width: 758px) {
  .about__list {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 90px 60px;
    counter-reset: item;
  }
}
@media (min-width: 1270px) {
  .about__list {
    grid-template-columns: repeat(4, 1fr);
    gap: 80px;
  }
}

.about__item {
  font-family: "Lato", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  color: #444444;
  margin-bottom: 18px;
  padding-left: 82px;
  padding-top: 4px;
  padding-bottom: 4px;
  position: relative;
}
.about__item:last-child {
  margin-bottom: 0;
}
.about__item::before {
  content: "";
  position: absolute;
  display: block;
  width: 60px;
  height: 60px;
  background-color: #5eaa2f;
  background-repeat: no-repeat;
  background-position: center;
  top: 0;
  left: 0;
}
.about__item--healthy::before {
  background-image: url("../img/sprite.svg#healthy-about");
  background-size: 30px 32px;
}
.about__item--powder::before {
  background-image: url("../img/sprite.svg#powder-about");
  background-size: 26px 29px;
}
.about__item--food::before {
  background-image: url("../img/sprite.svg#food-about");
  background-size: 25px 30px;
}
.about__item--time::before {
  background-image: url("../img/sprite.svg#time-about");
  background-size: 26px 31px;
}
@media (min-width: 758px) {
  .about__item {
    font-family: "Lato", sans-serif;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    color: #444444;
    padding-left: 0;
    padding-right: 75px;
    padding-top: 110px;
    padding-bottom: 45px;
    margin-bottom: 0;
    position: relative;
  }
  .about__item::before {
    width: 80px;
    height: 80px;
    top: 0;
  }
  .about__item::after {
    font-family: "Oswald", sans-serif;
    font-size: 280px;
    line-height: 280px;
    color: #f2f2f2;
    position: absolute;
    top: -40px;
    z-index: -1;
    counter-increment: item;
    content: counter(item);
    width: 136px;
    right: 75px;
  }
}
@media (min-width: 1270px) {
  .about__item {
    padding-right: 0;
  }
  .about__item::after {
    right: 0;
  }
}

