.slider {
  display: grid;
  grid-template-columns: 1fr 1fr;
  position: relative;
}

.slider__button {
  position: absolute;
  width: 4px;
  height: 100%;
  border-radius: 2px;
  background-color: #666666;
  top: 0;
  left: calc(50% - 2px);
  margin: 0;
  padding: 0;
  border: none;
  cursor: pointer;
}
.slider__button::before {
  content: "";
  position: absolute;
  width: 40px;
  height: 40px;
  border: 2px solid #666666;
  border-radius: 100%;
  background-color: #ffffff;
  top: calc(50% - 20px);
  left: -18px;
}
.slider__button::after {
  content: "";
  position: absolute;
  display: block;
  mask: url("../img/sprite.svg#arrows-slider");
  background-color: #666666;
  width: 24px;
  height: 18px;
  top: calc(50% - 9px);
  left: -10px;
}
.slider__button:hover, .slider__button:hover::after {
  background-color: #68b738;
}
.slider__button:hover::before {
  border-color: #68b738;
}
.slider__button:active, .slider__button:active::after {
  background-color: #5eaa2f;
}
.slider__button:active::before {
  border-color: #5eaa2f;
}

.slider__image--before, .slider__image--after {
  width: 100%;
  height: 256px;
  object-fit: cover;
  object-position: left;
}
.slider__image--after {
  object-position: right;
}
@media (min-width: 758px) {
  .slider__image:first-of-type {
    height: 512px;
  }
  .slider__image:last-of-type {
    height: 512px;
  }
}