@use "variables" as v;
@use "mixins" as m;

.main-title {
  font-family: v.$primary-font;
  font-weight: 400;
  font-size: v.$font-size-xl;
  line-height: v.$line-height-2xl;
  margin: 0;
  max-width: 500px;

  @include m.breakpoint(medium) {
    font-size: v.$font-size-2xl;
    line-height: v.$line-height-4xl;
  }
}
